.WelcomeScreen {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  background: #f4f0ec;
}
.login-button {
  color: #494949;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  border: 4px solid #494949;
  display: inline-block;
  transition: all 0.4s ease 0s;
  min-width: 10rem;
  margin: 1rem;
}
.google-btn {
  width: 184px;
  height: 42px;
  margin: 1rem;
  background-color: #a8a8a8;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}
.google-btn:hover {
  box-shadow: 0 0 3px #9db7be;
  cursor: pointer;
}

.google-btn:active {
  background: #264a45;
}
.google-icon-wrapper {
  position: absolute;
  margin-top: 1px;
  margin-left: 1px;
  width: 40px;
  height: 40px;
  border-radius: 2px;
  background-color: #264a45;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}
.google-icon {
  position: absolute;
  margin-top: 11px;
  margin-left: -8px;
  width: 18px;
  height: 18px;
}
.btn-text-welcomeScreen {
  color: #264a45;
  font-size: 14px;
  letter-spacing: 0.2px;
  font-family: 'Roboto';
  background-color: #fff;
  width: 80%;
  height: 100%;
  text-align: center;
  border-radius: 5px;
  cursor: pointer;
  float: right;
}

.privacy {
  display: block;
  text-decoration: none;
  color: #494949;
}

.google-btn-welcomeScreen {
  width: 184px;
  height: 42px;
  margin: 1rem;
  background-color: #264a45;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}
